@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ecf0f1;
}

code {
  font-family: 'Poppins', sans-serif;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.main {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
  }
.playlist {
    width: 70%;
    display: flex;
    flex-direction: column;
}

.loading{
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading-img{
    width: 50px;
}

.error{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}

.text-error {
    font-size: 18px;
    color: #535c68;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .playlist {
        width: 90%;
    }

    .text-error{
        font-size: 16px;
    }
}

@media screen and (max-width: 768px) {
    .playlist {
        width: 95%;
    }

    .text-error{
        font-size: 14px;
    }
}

.item {
    height: 72px;
    display: grid;
    grid-template-columns: 10% 5% 10% 45% 10% 10% 10%;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas: "r1 r2 r3 r4 r5 r6 r7";
    background: #ffffff;
    margin-bottom: 12px;
    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
}

.r1 {
    grid-area: r1;
}

.r2 {
    grid-area: r2;
}

.r3 {
    grid-area: r3;
}

.r4 {
    grid-area: r4;
}

.r5 {
    grid-area: r5;
}

.r6 {
    grid-area: r6;
}

.r7 {
    grid-area: r7;
}


.subitem {
    display: flex;
    align-items: center;
    overflow: hidden;
}

.center {
    justify-content: center;
}

.space-between {
    justify-content: space-between;
}

.text-item {
    text-align: center;
    font-size: 24px;
}

.position {
    color: #30336b;
    font-size: 24px;
    font-weight: 600;
}

.track{
    color: #30336b;
    font-weight: 600;
    margin-right: 8px;
    overflow: hidden;
    text-overflow: ellipsis; 
    white-space:nowrap;
    height: 72px;
    vertical-align: middle;
    line-height: 72px;
}

.artist{
    color: #535c68;
    overflow: hidden;
    text-overflow: ellipsis; 
    white-space:nowrap;
    height: 72px;
    vertical-align: middle;
    line-height: 72px;
}

.cover-item {
    height: 56px;
    border-radius: 5px;
}

.icon-item {
    height: 22px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .item{
        height: 62px;
        grid-template-columns: 10% 5% 15% 40% 10% 10% 10%;
    }

    .column{
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }

    .position {
        font-size: 22px;
    }
    
    .text-item{
        font-size: 22px;
    }

    .cover-item {
        height: 48px;
    }

    .icon-item {
        height: 18px;
    }

    .track{
        height: 31px;
        vertical-align: middle;
        line-height: 31px;
    }

    .artist{
        height: 31px;
        vertical-align: middle;
        line-height: 31px;
    }
}

@media screen and (max-width: 768px) {
    .item{
        height: 52px;
        grid-template-columns: 10% 5% 15% 40% 10% 10% 10%;
    }

    .column{
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }

    .position {
        font-size: 16px;
    }
    
    .text-item{
        font-size: 16px;
    }

    .cover-item {
        height: 38px;
    }

    .icon-item {
        height: 12px;
    }


    .track{
        height: 26px;
        vertical-align: middle;
        line-height: 26px;
    }

    .artist{
        height: 26px;
        vertical-align: middle;
        line-height: 26px;
    }
    
}

.label {
    height: 48px;
    display: grid;
    grid-template-columns: 10% 15% 45% 10% 10% 10%;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas: "l1 l2 l3 l4 l5 l6";
    font-family: 'Poppins', sans-serif;
}

.l1 {
    grid-area: l1;
}

.l2 {
    grid-area: l2;
}

.l3 {
    grid-area: l3;
}

.l4 {
    grid-area: l4;
}

.l5 {
    grid-area: l5;
}

.l6 {
    grid-area: l6;
}

.sublabel {
    display: flex;
    align-items: center;
    overflow: hidden;
}

.text-label {
    font-size: 18px;
    color: #535c68;
}

.icon-label {
    height: 18px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .label{
        height: 38px;
        grid-template-columns: 10% 20% 40% 10% 10% 10%;
    }

    .text-label{
        font-size: 16px;
    }

    .icon-label {
        height: 16px;
    }

}

@media screen and (max-width: 768px) {
    .label{
        height: 32px;
        grid-template-columns: 10% 20% 40% 10% 10% 10%;
    }

    .text-label{
        font-size: 14px;
    }

    .icon-label {
        height: 14px;
    }
}

.header{
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Poppins', sans-serif;
}

.container-title{
    margin: 52px 0;
    font-size: 28px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.title{
    font-size: 62px;
    text-transform: uppercase;
    color: #30336b;
    margin: 0;
    font-weight: 600;
}

.subtitle{
    font-size: 28px;
    color:#535c68;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .header{
        width: 90%;
    }

    .container-title{
        margin: 42px 0;
    }

    .title {
        font-size: 52px;
    }

    .subtitle{
        font-size: 22px;
    }
}

@media screen and (max-width: 768px) {
    .header{
        width: 95%;
    }

    .container-title{
        margin: 32px 0;
    }

    .title {
        font-size: 26px;
    }

    .subtitle{
        font-size: 16px;
    }
}

.menu{
    width: 100%;
    height: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    border-bottom: 1px solid #535c68;
}

.item-menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.text-menu{
    font-size: 18px;
    color: #535c68;
    margin-right: 8px;
}

.select-menu{
    font-size: 18px;
    color: #535c68;
    background: #ecf0f1;
    border-radius: 5px;
    outline: 0;
    height: 28px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .menu{
        height: 38px;
    }

    .text-menu{
        font-size: 16px;
    }

    .select-menu{
        font-size: 16px;
    }

    .select-menu{
        height: 26px;
    }
}

@media screen and (max-width: 768px) {
    .menu{
        height: 32px;
    }

    .text-menu{
        font-size: 14px;
    }

    .select-menu{
        font-size: 14px;
    }

    .select-menu{
        height: 24px;
    }
}
.footer{
    height: 100px;
    font-family: 'Poppins', sans-serif;
    width: 70%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas:
        "f1 f1 f1"
        "f2 . f3";
}

.f1 { 
    grid-area: f1; 
    justify-content: center;
}

.f2 { 
    grid-area: f2; 
    justify-content: flex-start;
}

.f3 { 
    grid-area: f3; 
    justify-content: flex-end;
}

.subfooter{
    display: flex;
    align-items: center;
}

.social{
    width: 140px;
    display: flex;
    justify-content: space-between;
}

.socialicon{
    height: 18px;
}

.text-footer{
    font-size: 18px;
    color:#535c68;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .footer{
        width: 90%;
        height: 80px;
        margin-bottom: 22px;
    }

    .text-footer{
        font-size: 16px;
    }

    .social{
        width: 100px;
    }

    .socialicon{
        height: 16px;
    }
}

@media screen and (max-width: 768px) {
    .footer{
        width: 95%;
        height: 70px;
        margin-bottom: 22px;
    }

    .text-footer{
        font-size: 14px;
    }

    .social{
        width: 80px;
    }

    .socialicon{
        height: 14px;
    }
}

